@import url('https://fonts.googleapis.com/css2?family=Archivo:wght@300&family=DM+Serif+Display:ital@0;1&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Anek+Telugu:wght@400;500;600;700;800&display=swap');

*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
body {
  margin: 0;
font-family: 'Anek Telugu', sans-serif;
  color: white;
  background-color: black;
  background:linear-gradient(0deg, rgba(0, 0, 0), rgba(0, 0, 0, 0.6)), url(./assets/leaves.webp);
  background-position: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

#root{
  display: grid;
  grid-template-rows: auto 1fr;
  min-height: 100vh;
  margin-bottom: 3rem;
}
a{
  color: white;
}
.logo{
  height: 2rem;
}

.cover {
  position: absolute;
  height: 100vh;
  width: 100%;
  z-index: 50;
  background-color: tomato;
  animation: slide forwards 1500ms;
}

@keyframes slide {
  0% {
    height: 100vh;
  }
  100% {
    height: 0;
  }
}

html{
  font-size: 19px;
}
:root{
  --tomato: tomato;  
}
header{
  position: relative;
  width: 100%;
}
header nav{
  display: grid;
  grid-template-columns: repeat(2,1fr);
  padding: .8rem 1rem;
}

header nav a{
  text-decoration: none;
  display: flex;
  align-items: center;
  cursor: pointer;
}
header nav span{
  color: white;
}

.overlay-menu{
  width: 100%;
  position: fixed;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 30;
    cursor: pointer;
}

.overlay-menu .menu-bar{
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: space-around;
  box-shadow: -1px -3px 35px -12px rgba(0,0,0,0.47);
-webkit-box-shadow: -1px -3px 35px -12px rgba(0,0,0,0.47);
-moz-box-shadow: -1px -3px 35px -12px rgba(0,0,0,0.47);
}

.overlay-menu .menu-bar a{
text-decoration: none;
display: flex;
flex-direction: column;
align-items: center;
padding: 0 .4rem;
transition: all ease-in-out 200ms;
}


  .overlay-menu .menu-bar i{
font-size:1.5rem;
}
.overlay-menu .menu-bar span{
  font-size: .8rem;
  }
  .overlay-menu .active-menu{
    border-top:solid;
  }

/* NAVBAR MENU */

  nav .menu-bar{
    gap: 3rem; 
    font-size: .8rem;
    font-weight: 500;
  }

nav button{
  border: none;
  outline: none;
  background-color: rgba(0,0,0,0);
}

nav .active-nav{
  border-bottom:solid;
}

.about{
  min-height: 95vh;
  display: flex;
  flex-direction: column;
  padding: 1rem;
  gap: 3rem;
}
.about > section{
  width: 100%;
}
.about h4{
  padding: .3rem .6rem;
  font-weight: 500;
}

.about .picture{
  height: 11rem;
  width: 11rem;
  display: block;
  margin: auto;
  filter: grayscale(1);
}

.certification{
  display: grid;
  grid-template-columns: auto 1fr auto;
  align-items: center;
  gap: .7rem;
  padding: .4rem 0;
}
.certification p{
  margin: 0;
}
.certification a {
  padding: .3rem .5rem;
  text-decoration: none;
  font-family: 'Archivo', sans-serif;
  align-self: center;
}
.certification a:hover {
color: white;
text-decoration: underline;
}


.hero{
  height: 100%;
}

.hero .hero-details{
  position: relative;
  height: 100%;
 margin: 0 auto;
 color: white;
 padding: 2rem;
}
.hero .hero-details > div{
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: .8rem;
}
.hero .hero-details .resume-btn{
  color: white;
  font-family: 'Archivo', sans-serif;
  font-size: 1rem;
  transition: background 0.4s linear;
  padding: .6rem .8rem;
text-decoration: none;
border-radius: .3rem;
}
 .hero-details .resume-btn:hover{
transition: all 300ms ease-in-out;
filter: brightness(90%);
 transform: scale(.95);
}
  .hero-details .social{
    text-decoration: none;
    font-size: 2rem;
    transition: all ease-in-out 300ms;
    filter: brightness(0) invert(1);
  }
  .hero-details .social:hover{
    filter: brightness(1) invert(0);
  }
  .hero-details h1{
font-size: 2rem;
  }

.hero .themes p{
height: 2rem;
width: 2rem;
border-radius: 10%;
transition: all ease-in-out 200ms;
cursor: pointer;
  }
  .hero .themes p:hover{
  transform: scale(.9);
  }
  
.hero .themes p.darkolivegreen{
  background-color:darkolivegreen;
  }
  .hero .themes p.cadetblue{
    background-color: cadetblue;
  }
  .hero .themes p.tomato{
    background-color:tomato;
  }
  .hero .themes p.darkgoldenrod{
    background-color:darkgoldenrod;
  }

  .skills h2 span{
    color: rgb(26, 26, 26);
    font-size: .9rem;
    font-weight: bolder;
  }
  .skills h2{
    color: var(--tomato);
    font-weight: 800;
  }
.skills-carousel > div{
 display: grid;
 grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
justify-content: center;
}
.skills-carousel > div i.fa-brands{
  filter: grayscale(2);
  font-size: 3.5rem;
  display: block;
  margin: auto;
}
.skills-carousel > div i.animation-color{
  filter: grayscale(0);
 transform: scale(1.6);
 transition: all ease-in-out 200ms;
}


.project-card{
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  width: 90%;
  margin: auto;
}


.project-card >div:nth-child(1){
 height: 17rem;
}
.project-card >div:nth-child(2){
  position: relative;
  top: -20%;
  width: 95%;
  margin: auto;
}
.project-details{
  display: grid;
  text-align: right;
  grid-template-rows: 1fr auto;
  z-index: 10;
}
.project-description{
  background-color: #111;
 display: grid;
 grid-template-rows: auto auto 3fr auto;
 border-radius: .8rem
}
.project-description a:hover{
  filter: brightness(0) invert(1);
}
.project-image{
  position: relative;
  color: #fff;
  transition: linear all ease-in-out 500ms;

}
.project-image > div{
  position: absolute;
  height: 100%;
  width: 100%;
  z-index: 1;
  opacity: 0;
}
.project-image:hover > div{
  opacity: 0;
}
.project-image > img{
  position: absolute;
  height: 100%;
  width: 100%;
  object-fit: fill;
  transition: linear all ease-in-out 500ms;
}
/*.project-image:hover > img{
  filter: grayscale(0);
}*/
.contact {
  position: relative;
}
.contact form {
  width: 100%;
  margin: auto;
  padding: 1rem;
}
.contact form i {
  display: block;
  margin: auto;
  font-size: 3rem;
}
.contact form input, .contact form textarea{
  border: none;
  outline: none;
  background-color: #333;
  color: white;
  width: 100%;
  padding: .4rem;
  min-height:2.5rem;
}

.contact .social-links{
  position: absolute;
  top: 100%;
  left: 5%;
  font-size: 1.8rem;
}
.contact .social-links a{
  transition: all ease-in-out 200ms;
}


.contact .social-links a:hover{
  filter: brightness(0) invert(1);
  transform: translateY(-.4rem);
}
.contact form button{
  border: none;
  outline: none;
  color: white;
}
.contact form button:hover{
 transition: all ease-in-out 200ms;
 transform: scale(.93);
 filter: brightness(80%);
}















footer{
  background-color: rgb(0, 0, 0);
}

@media(max-width:375px) {
  html {
      font-size: 14px;
  }

}

@media(min-width:376px) and (max-width:575px) {
  html {
      font-size: 16px;
  } 
}


@media(min-width:576px) and (max-width:820px) {
  html {
      font-size: 18px;
  } 
  

}

@media(min-width: 821px){
  .skills-carousel > div{
    grid-template-columns: repeat(4, 1fr);
     gap: 2rem;
   }

  .project-card{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    margin: 3rem auto;
  }
  
  .project-card >div:nth-child(2){
    position: relative;
    top: 0%;
    right: 10%;
    width: 100%;
    margin: 0;
  }
  .project-details{
    text-align: right;
  }
  .contact form {
    width: 65%;
    margin: auto;
  }
  .contact .social-links{
    position: absolute;
    top: 50%;
    left: 0%;
  }
  #root{
    margin-bottom: 0rem;
  }
}
